.googleMap {
    height: 450px;
    width: 500px;
}

.stats {
	width: 10%;
}

.MarginSpacer {
	margin-left: 5px;
}

.container {
	width: 100%;
	padding: 0;
	margin: 0;
}

/*Create an account button*/
div.btn-group > a > .btn-primary {
	background-image: -webkit-gradient(linear,left top, left bottom,color-stop(0, #000000),to(#AAAAAA));
	background-image: -webkit-linear-gradient(top,#000000 0,#AAAAAA 100%);
	background-image: -o-linear-gradient(top,#000000 0,#AAAAAA 100%);
	background-image: linear-gradient(to bottom,#000000 0,#AAAAAA 100%);
}

/*Create an account button hover*/
div.btn-group > a > .btn-primary:hover, div.btn-group > a > .btn-primary:focus{
	background-position: 0 -32px;
	background-image: -webkit-gradient(linear,left top, left bottom,color-stop(0, #333333),to(#AAAAAA));
	background-image: -webkit-linear-gradient(top,#333333 0,#AAAAAA 100%);
	background-image: -o-linear-gradient(top,#333333 0,#AAAAAA 100%);
	background-image: linear-gradient(to bottom,#333333 0,#AAAAAA 100%);
	background-color: grey;
}

/*Change options on navbar text items (signed in as:)*/
p.navbar-text {
	margin-top: 5px;
	font-size: 15px;
}

/*make signed in black*/
.navbar-default .navbar-text {
	color: #000000;
}

.navbar {
	margin-bottom: 0px;
}

div.tableInfo {
	border-width: 3px;
	border-color: black;
	padding-right: 25px;
	padding-left: 5px;
	padding-bottom: 3px;
	font-size: 25px;
	border: solid;
}

/*background color/ tournament table*/
.tourneyList {
	background-color:#DDDDDD;
	margin: 5px;
	width: auto;
}

.navbar, .navbar-default {
	background-image: -webkit-gradient(linear, left top, left bottom, color-stop(.25, #CCCCCC), to(#555555)) !important;
	background-image: -webkit-linear-gradient(top, #CCCCCC .25, #555555 100%) !important;
	background-image: -o-linear-gradient(top, #CCCCCC .25, #555555 100%) !important;
	background-image: linear-gradient(to bottom, #CCCCCC .25, #555555 100%) !important;
	border-color: grey;
}

/*main logo styling*/
.navbar-default .navbar-brand {
	color: #000000;
	font-size: 30px;
}

/*main logo styling on hover*/
.navbar-default .navbar-brand:hover{
	color: #FFFFFF;
}

/*styling for the dropdown menus*/
a.dropdown-toggle {
	color: #000000;
	font-size: 20px;
}

/*styling for the dropdown menus on hover*/
a.dropdown-toggle:hover {
	text-decoration: none;
	color: #FFFFFF;
}

.navbar-header a{
	color: black;
}

.navbar>.container .navbar-brand, .navbar>.container-fluid .navbar-brand {
	margin: 0;
}

.navbar-right{
	margin: 0;
}

/*form styling used on the various forms throughout app*/
.FormStyling {
	padding: 20px;
	width: 25%;
	height: auto;
	background-color: #DDDDDD;
	border: 2px;
	border-style: solid;
	border-color: #FFFFFF;
	border-radius: 3px;
}

.TournamentForm {
	padding: 20px;
	width: 25%;
	height: auto;
	background-color: #DDDDDD;
	border: 2px;
	border-style: solid;
	border-color: #FFFFFF;
	border-radius: 3px;
}




